html {
  background-color: black;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(17, 17, 17);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  color: white !important;
}

.App {
  text-align: center;
}

h1 {
  margin: 0 0 20px 0 !important;
}

footer {
  position:fixed !important;
  left:0 !important;
  right: 0 !important;
  bottom: 0 !important;
  font-size: 12px !important;
  padding: 10px 0 0 0 !important;
  height: 35px;
  background-color: #232323;
  width: inherit !important;
}

#copy {
  color: white !important;
  margin: 0 !important;
  font-weight: light !important;
}
* {
    color: white !important;
  }
  
  
  header {
    display: flex;
    flex-wrap: wrap;
    padding: 10px 20px;
    align-items: center;
    background-color: #232323;
    position: fixed;
    justify-content: space-between;
    left: 0;
    right: 0;
    top: 0;
  }
  
  .logo {
    padding: 0px 20px;
    height: auto !important;
    width: auto !important;
    filter: none !important;
    -webkit-filter: none !important;
  }

  .logo:hover {
    -webkit-transform: none !important;
            transform: none !important;
  }
  
  a {
      text-decoration: none !important;
      margin: 0 5px;
  }

  a:hover {
    color: #8c7322 !important; 
    transition: 0.5s;
  }
  
  .active {
    color: #efd374 !important; 
  }

  main {
    margin-top: 80px;
    background-color: black;
  }
  
  .ms-auto {
    align-items: center;
  }

  @media (max-width: 991px){
    .ms-auto {
      position:absolute;
      right:0;
      background-color: #232323;
      padding: 5px;
      min-width: 150px;
    }
    a {
      border-top: 1px solid rgb(53, 53, 53);
      width: 100%;
      padding: 5px 0 5px 0;
    }
  }
  
  span {
    color: #636363 !important;
    margin: 0 5px;
  }
  
  @media (max-width: 991px){
    .barra {
        opacity: 0;
        font-size: 0;
    }
  }




.col {
    border: 0.01px dotted darkslategrey;
    max-height: 750px !important;
    padding: 20px 5px 125px 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    
    
    
}

p {
    color: #fff !important;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
}
img {
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

img:hover {
    filter: drop-shadow(0px 0px 10px rgb(250, 253, 159));
    -webkit-filter: drop-shadow(0px 0px 10px rgb(250, 253, 159));
    transition: all 1s ease;
}

.gazetteer {
    width: 100%;
}

.gazetteer:hover {
    -webkit-transform: none !important;
            transform: none !important;
    filter: none !important;
    -webkit-filter: none !important;
}

@media (min-width: 576px){
    img:hover {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
}

@media (min-width: 698px){
    img:hover {
        -webkit-transform: scale(1.5) !important;
                transform: scale(1.5) !important;
    }
}

.modal-header {
    border: none;
}

#modal-title {
    color: black !important;
}

.modal-90w {
    min-width: 90%;
}




.contenedor-botones {
    display: flex;
    justify-content: space-around;
    height: 35px;
    margin: 10px 0 10px 0;
}

.col-boton {
    border: none !important;
    padding: 0 !important;
    display: flex;
    justify-content: space-around !important;
}

.boton {
    width: 90% !important;
    margin: 5px;
    align-self: center;
}

.sub {
    color: white !important;
    font-size: 11px;
    display: block;
    margin-top: 0 !important;
}
